  import { Component } from '@angular/core';
import { IonicPage, NavController, ModalController, NavParams } from 'ionic-angular';
import { GlobalVarsProvider } from '../../providers/global-vars/global-vars';
import { AlertServiceProvider } from '../../providers/alert-service/alert-service';
import { ClientdataProvider } from '../../providers/clientdata/clientdata';
import { ValidatorProvider } from '../../providers/validator/validator'
import { ContactsProvider } from '../../providers/contacts/contacts';
import Format from 'string-format';


@IonicPage()
@Component( {
  selector: 'page-airtime',
  templateUrl: 'airtime.html',
} )
export class AirtimePage {
  objectblock: any = { receiverno: '', amount: '', lang: '', account_from: '', biller_ref: '', receiver: 'showmine', username: '' };

  accounts: any = [];
  responded: boolean = false;

  actualBalance: number;
  availableBalance: number;
  error: number = 0;
  datetime: string;
  body: any;
  data: any;
  header: any;
  token: any;
  customer: any;
  cname: string = "";
  unmasked_accounts: any = [];
  confirmReq: any;
  title: any;
  langs: any;
  field100: any = "TIGOTOPUP";
  avail: boolean = false;
  constructor( public modalCtrl: ModalController, public contactsProvider: ContactsProvider, public validator: ValidatorProvider, private clientdata: ClientdataProvider, public alertService: AlertServiceProvider,
    public globalVars: GlobalVarsProvider, public navCtrl: NavController, public navParams: NavParams ) {
    this.objectblock.receiver = 'showmine';
    this.data = navParams.get( "data" );
    this.title = this.data.title;
    this.langs = this.globalVars.doTranslate( this.globalVars.getLang() );
    this.objectblock.username = this.globalVars.Username;
    this.accounts = globalVars.linked_acc;
    this.objectblock.account_from = this.accounts[ 0 ].acc_number;
    this.customer = globalVars.customer_details;
    this.objectblock.recepient = "my";
    this.avail = this.data.active;

    console.log("over here");
    // this.avail = false;  
    if ( this.title == "Halotel" ) {
      this.objectblock.field100 = "HALOTELTOPUP";
    }
    else if ( this.title == "Zantel" ) {
      this.objectblock.field100 = "ZANTELTOPUP";
    }
    else if ( this.title == "Tigo" ) {
      this.objectblock.field100 = "TIGOTOPUP";
    }
    else if ( this.title == "Airtel" ) {
      this.objectblock.field100 = "AIRTELTOPUP";
    }
    else if ( this.title == "Vodacom" ) {
      this.objectblock.field100 = "VODACOMTOPUP";
    }
    else if ( this.title == "TTCL" ) {
      this.objectblock.field100 = "TTCLTOPUP";
    }
    this.checkRecepeint( this.objectblock.recepient );
  }

  ionViewDidLoad() {
    console.log( 'ionViewDidLoad AirtimePage' );
  }
  goHome() {
    this.navCtrl.setRoot( 'HomePage' );
  }

  checkRecepeint( recepient ) {
    if ( recepient == 'my' ) {

      console.log("my.............")
      this.objectblock.account_to = this.globalVars.getUsername();
    }
   
  }

  pickContact() {
    this.objectblock.account_to = "";
    this.contactsProvider.pickContact().then( contact => {
      if ( contact.phoneNo.length > 1 ) {
        if ( contact.phoneNo[ 0 ] == "" || contact.phoneNo[ 0 ] == undefined ) {

          this.alertService.mytoastdown( this.langs.valid_topup_number );
        } else {
          this.objectblock.account_to = contact.phoneNo[ 0 ];
        }
      }
      else {
        if ( contact.phoneNo[ 0 ] == "" || contact.phoneNo[ 0 ] == undefined ) {
          this.alertService.mytoastdown( this.langs.valid_topup_number );
        } else {
          this.objectblock.account_to = contact.phoneNo[ 0 ];
        }
      }
    } );
  }
  

  

  submit() {

    console.log("dfghjgyuioipok...............................",this.objectblock.account_to);
    console.log( this.objectblock );
    if ( this.objectblock.account_from == null || this.objectblock.account_from == "" ) {
      this.error = 1;
    } else if ( this.objectblock.account_to == undefined || this.objectblock.account_to == "" || this.objectblock.account_to == null  ) {
      this.error = 2;
    }
    else if ( this.objectblock.amount === undefined || this.objectblock.amount === "" || this.objectblock.amount === undefined || this.objectblock.amount != this.globalVars.cleanDigit( this.objectblock.amount ) ) {
      this.error = 3;
    }
    let message = Format( this.langs.topup_meso, this.title, this.objectblock );
    let template = "<div>" + message + "</div>";
    let obj = { body: "", template: template, ttype: "", completed: false, pageTo: '' };
    let myModal = this.modalCtrl.create( 'ConfirmModalPage', obj );
    myModal.present();
    myModal.onDidDismiss( data => {
      if ( data ) { this.request(); }
    } );

  }

  ionViewDidEnter() {
    this.globalVars.reload();

  }

  request() {
    this.alertService.showLoading( this.langs.submitting_txt );
    this.clientdata.airtime( this.objectblock )
      .subscribe( data => {
        data = this.globalVars.testdec( data );
        console.log( "hereeeeee",data )
        if ( data.length != 0 ) {
          console.log( data );
          this.alertService.dismiss();
          if ( !data.error ) {
            this.alertService.errorPop( data.message );
            this.navCtrl.pop();
          } else if ( data.error ) {
            this.alertService.errorPop( data.message );
            this.navCtrl.pop();
          } else {
            this.alertService.errorPop( this.langs.tech );
            this.navCtrl.pop();
          }
        } else {
          this.alertService.dismiss();
          this.alertService.errorPop( this.langs.tech );
          this.navCtrl.pop();
        }
      }, err => {
        console.log( err );
        this.alertService.dismiss();
        this.alertService.errorPop( this.langs.tech );
      } );
  }
}



